	module.exports = {"data":{"post":{"html":"<p>Mama left Thursday for her 10 day business trip in Sydney. So we men have to fend for ourselves for a few days. Our highlight today combines fun and technology. This morning we'll be taking the BUS from Buchholz to Harburg, then board the I.C.E. TRAIN to Hannover, where we will then hop on the STREETCAR to head for a day at the ZOO. If it rains, we'll just ride the streetcar all afternoon. Then we do the whole thing again in reverse.</p>\n<p>With a couple of Oma visits, a lantern march in kindergarten, an appointment at the ear-nose-throat specialist, and today's expedition, the week is going to go by very quickly. Then on Sunday we'll get up early and head to the airport to pick up Mama, whether she wants to be picked up or not.</p>","fields":{"title":"Sydney and Hannover","date":"06 Nov 2004","url":"https://papascott.de/2004-11-06-sydney-and-hannover/"}}},"pathContext":{"slug":"/2004-11-06-sydney-and-hannover/","prev":{"fields":{"title":"Party like it's 1989","date":"05 Nov 2004","slug":"/2004-11-05-party-like-its-1989/"}},"next":{"fields":{"title":"The Values-Vote Myth","date":"06 Nov 2004","slug":"/2004-11-06-the-valuesvote-myth/"}}}}

